import { Card, Row, Col, Statistic, Space, Tag, Typography } from 'antd';
import { NodeIndexOutlined } from '@ant-design/icons';
import PropTypes from 'prop-types';

const { Text } = Typography;

/**
 * GraphStatistics Component
 * Displays statistics about the dependency graph including nodes, edges, types, and bottlenecks
 */
const GraphStatistics = ({ metadata }) => {
  if (!metadata) return null;

  const { nodeCount, edgeCount, resourceTypeCounts, bottlenecks } = metadata;

  return (
    <Card title="Graph Statistics" style={{ marginTop: 16 }}>
      <Row gutter={[16, 16]}>
        <Col xs={12} sm={8} md={6}>
          <Statistic
            title="Total Resources"
            value={nodeCount}
            valueStyle={{ color: '#1890ff' }}
            prefix={<NodeIndexOutlined />}
          />
        </Col>
        
        <Col xs={12} sm={8} md={6}>
          <Statistic
            title="Dependencies"
            value={edgeCount}
            valueStyle={{ color: '#52c41a' }}
          />
        </Col>
        
        <Col xs={12} sm={8} md={6}>
          <Statistic
            title="Resource Types"
            value={Object.keys(resourceTypeCounts || {}).length}
            valueStyle={{ color: '#faad14' }}
          />
        </Col>
        
        <Col xs={12} sm={8} md={6}>
          <Statistic
            title="Bottlenecks"
            value={bottlenecks?.length || 0}
            valueStyle={{ 
              color: bottlenecks?.length > 0 ? '#ff4d4f' : '#52c41a' 
            }}
          />
        </Col>
      </Row>
      
      {resourceTypeCounts && Object.keys(resourceTypeCounts).length > 0 && (
        <>
          <div style={{ marginTop: 24, marginBottom: 12 }}>
            <Text strong>Resource Type Distribution</Text>
          </div>
          <Space wrap>
            {Object.entries(resourceTypeCounts).map(([type, count]) => (
              <Tag key={type} color="blue">
                {type}: {count}
              </Tag>
            ))}
          </Space>
        </>
      )}
    </Card>
  );
};

GraphStatistics.propTypes = {
  metadata: PropTypes.shape({
    nodeCount: PropTypes.number,
    edgeCount: PropTypes.number,
    resourceTypeCounts: PropTypes.object,
    bottlenecks: PropTypes.array,
  }),
};

export default GraphStatistics;
