import PropTypes from 'prop-types';
import { Card, Row, Col, Typography, Alert } from 'antd';

const { Title, Text } = Typography;

const StateOverview = ({ infrastructureData }) => {
  if (!infrastructureData) return null;

  // Handle different response structures - similar to old UI logic
  let actualState = infrastructureData;
  let dataSource = 'direct';
  let resourceCount = 0;

  // Check if we have discovered resources and managed state is empty or doesn't exist
  const hasDiscoveredResources = infrastructureData.resources && infrastructureData.resources.length > 0;
  const managedResourceCount = infrastructureData.managed_state ? 
    Object.keys(infrastructureData.managed_state.resources || {}).length : 0;

  if (infrastructureData.managed_state && managedResourceCount > 0) {
    // Use managed state if it has resources
    actualState = infrastructureData.managed_state;
    dataSource = 'managed';
    resourceCount = managedResourceCount;
  } else if (hasDiscoveredResources) {
    // Use discovered resources if managed state is empty
    dataSource = 'discovered';
    resourceCount = infrastructureData.count || infrastructureData.resources.length;
  } else {
    // Direct resource count from API response
    resourceCount = infrastructureData.count || (infrastructureData.resources?.length || 0);
  }

  return (
    <>
      <div className="infrastructure-state-overview">
        <Row gutter={[16, 16]} style={{ marginBottom: 24 }}>
          <Col xs={24} sm={6}>
            <Card>
              <div style={{ textAlign: 'center' }}>
                <Title level={2} style={{ margin: 0, color: '#1890ff' }}>
                  {resourceCount}
                </Title>
                <Text type="secondary">
                  {dataSource === 'discovered' ? 'Discovered Resources' : 'Managed Resources'}
                </Text>
              </div>
            </Card>
          </Col>
          
          <Col xs={24} sm={6}>
            <Card>
              <div style={{ textAlign: 'center' }}>
                <Title level={2} style={{ margin: 0, color: '#52c41a' }}>
                  {actualState.version || '1.0.0'}
                </Title>
                <Text type="secondary">State Version</Text>
              </div>
            </Card>
          </Col>
          
          <Col xs={24} sm={6}>
            <Card>
              <div style={{ textAlign: 'center' }}>
                <Title level={2} style={{ margin: 0, color: '#faad14' }}>
                  {new Date(actualState.lastUpdated || actualState.last_updated || infrastructureData.timestamp || Date.now()).toLocaleDateString()}
                </Title>
                <Text type="secondary">Last Updated</Text>
              </div>
            </Card>
          </Col>

          <Col xs={24} sm={6}>
            <Card>
              <div style={{ textAlign: 'center' }}>
                <Title level={2} style={{ margin: 0, color: '#17a2b8' }}>
                  {actualState.region || 'N/A'}
                </Title>
                <Text type="secondary">Region</Text>
              </div>
            </Card>
          </Col>
        </Row>
      </div>

      {dataSource === 'discovered' && (
        <Alert
          message={
            <span>
              <i className="fas fa-cloud" style={{ marginRight: 8 }} />
              Showing live AWS resources (no managed state found)
            </span>
          }
          type="info"
          showIcon={false}
          style={{ marginBottom: 16 }}
        />
      )}

      {actualState.error && (
        <Alert
          message={`Error: ${actualState.error}`}
          type="error"
          style={{ marginBottom: 16 }}
        />
      )}
    </>
  );
};

StateOverview.propTypes = {
  infrastructureData: PropTypes.shape({
    count: PropTypes.number,
    resources: PropTypes.array,
    managed_state: PropTypes.object,
    timestamp: PropTypes.string,
    error: PropTypes.string,
  }),
};

export default StateOverview;