import { useState } from 'react';
import PropTypes from 'prop-types';
import { Card, Typography, Collapse, Button } from 'antd';
import { ExpandOutlined, CompressOutlined } from '@ant-design/icons';

const { Text, Title } = Typography;
const { Panel } = Collapse;

const ResourceCard = ({ resource, searchTerm, iconUrl }) => {
  const [isExpanded, setIsExpanded] = useState(false);
  
  // Extract resource properties with fallback handling
  const displayProperties = resource.properties?.properties || resource.properties || {};
  
  // Highlight search term in resource ID
  const highlightSearchTerm = (text, searchTerm) => {
    if (!searchTerm) return text;
    
    const regex = new RegExp(`(${searchTerm})`, 'gi');
    const parts = text.split(regex);
    
    return parts.map((part, index) => 
      regex.test(part) ? (
        <span key={index} className="search-highlight">
          {part}
        </span>
      ) : part
    );
  };

  return (
    <Card
      className="resource-card"
      hoverable
      bodyStyle={{ padding: '20px' }}
    >
      <div className="resource-header">
        <div style={{ display: 'flex', alignItems: 'center', gap: '12px', marginBottom: '16px' }}>
          <div className="resource-icon">
            <img 
              src={iconUrl}
              alt={`${resource.type} icon`}
              style={{ width: '32px', height: '32px' }}
              onError={(e) => {
                e.target.src = '/aws-service-icons/management-governance/32/Arch_AWS-CloudFormation_32.svg';
              }}
            />
          </div>
          <div style={{ flex: 1, minWidth: 0 }}>
            <Title 
              level={5} 
              style={{ 
                margin: 0, 
                fontSize: '16px',
                fontWeight: 600,
                color: '#1a1a1a',
                wordBreak: 'break-all'
              }}
            >
              {highlightSearchTerm(resource.id, searchTerm)}
            </Title>
            <Text 
              type="secondary" 
              style={{ 
                fontSize: '12px',
                textTransform: 'uppercase',
                fontWeight: 500,
                letterSpacing: '0.5px'
              }}
            >
              {resource.type}
            </Text>
          </div>
          <Button
            type="text"
            size="small"
            icon={isExpanded ? <CompressOutlined /> : <ExpandOutlined />}
            onClick={() => setIsExpanded(!isExpanded)}
            style={{
              color: '#666',
              opacity: 0.7,
            }}
          />
        </div>
      </div>

      <Collapse
        ghost
        activeKey={isExpanded ? ['properties'] : []}
        onChange={() => setIsExpanded(!isExpanded)}
        style={{ border: 'none' }}
      >
        <Panel 
          key="properties" 
          header={null}
          showArrow={false}
          style={{ padding: 0, border: 'none' }}
        >
          <div className="properties-container">
            <pre 
              style={{ 
                margin: 0,
                padding: '16px',
                fontSize: '12px',
                lineHeight: '1.5',
                fontFamily: 'SF Mono, Monaco, Consolas, monospace',
                color: '#2d3748',
                backgroundColor: 'transparent',
                whiteSpace: 'pre-wrap',
                wordBreak: 'break-word',
              }}
            >
              {JSON.stringify(displayProperties, null, 2)}
            </pre>
          </div>
        </Panel>
      </Collapse>
    </Card>
  );
};

ResourceCard.propTypes = {
  resource: PropTypes.shape({
    id: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired,
    properties: PropTypes.object,
  }).isRequired,
  searchTerm: PropTypes.string,
  iconUrl: PropTypes.string.isRequired,
};

ResourceCard.defaultProps = {
  searchTerm: '',
};

export default ResourceCard;