import { useEffect, useRef, useState, useCallback } from 'react';
import { Card, Space, Typography, Tooltip, Button, Tag, Modal } from 'antd';
import { NodeIndexOutlined, FullscreenOutlined, ZoomInOutlined, ZoomOutOutlined, CompressOutlined, DragOutlined, AimOutlined } from '@ant-design/icons';
import PropTypes from 'prop-types';
import mermaid from 'mermaid';

const { Text } = Typography;

// AWS service icon mapping (same as ResourcesByCategory.js)
const getAWSServiceIcon = (resourceType) => {
  const typeMapping = {
    // Compute
    'ec2_instance': 'compute/32/Arch_Amazon-EC2_32.svg',
    'instance': 'compute/32/Arch_Amazon-EC2_32.svg',
    'ec2': 'compute/32/Arch_Amazon-EC2_32.svg',
    'ami': 'compute/32/Arch_Amazon-EC2_32.svg',
    'lambda': 'compute/32/Arch_AWS-Lambda_32.svg',
    'auto_scaling_group': 'compute/32/Arch_Amazon-EC2-Auto-Scaling_32.svg',
    'asg': 'compute/32/Arch_Amazon-EC2-Auto-Scaling_32.svg',
    
    // Networking
    'vpc': 'networking-content-delivery/32/Arch_Amazon-Virtual-Private-Cloud_32.svg',
    'subnet': 'networking-content-delivery/32/Arch_Amazon-Virtual-Private-Cloud_32.svg',
    'security_group': 'security-identity-compliance/32/Arch_AWS-WAF_32.svg',
    'securitygroup': 'security-identity-compliance/32/Arch_AWS-WAF_32.svg',
    'sg': 'security-identity-compliance/32/Arch_AWS-WAF_32.svg',
    'load_balancer': 'networking-content-delivery/32/Arch_Elastic-Load-Balancing_32.svg',
    'loadbalancer': 'networking-content-delivery/32/Arch_Elastic-Load-Balancing_32.svg',
    'alb': 'networking-content-delivery/32/Arch_Elastic-Load-Balancing_32.svg',
    'lb': 'networking-content-delivery/32/Arch_Elastic-Load-Balancing_32.svg',
    'route53': 'networking-content-delivery/32/Arch_Amazon-Route-53_32.svg',
    'cloudfront': 'networking-content-delivery/32/Arch_Amazon-CloudFront_32.svg',
    'internet_gateway': 'networking-content-delivery/32/Arch_Amazon-Virtual-Private-Cloud_32.svg',
    'nat_gateway': 'networking-content-delivery/32/Arch_Amazon-Virtual-Private-Cloud_32.svg',
    'natgw': 'networking-content-delivery/32/Arch_Amazon-Virtual-Private-Cloud_32.svg',
    'route_table': 'networking-content-delivery/32/Arch_Amazon-Virtual-Private-Cloud_32.svg',
    
    // Database
    'rds': 'database/32/Arch_Amazon-RDS_32.svg',
    'database': 'database/32/Arch_Amazon-RDS_32.svg',
    'dynamodb': 'database/32/Arch_Amazon-DynamoDB_32.svg',
    'redshift': 'database/32/Arch_Amazon-Redshift_32.svg',
    
    // Storage
    's3': 'storage/32/Arch_Amazon-Simple-Storage-Service_32.svg',
    'ebs': 'storage/32/Arch_Amazon-Elastic-Block-Store_32.svg',
    'efs': 'storage/32/Arch_Amazon-Elastic-File-System_32.svg',
    
    // Other
    'tg': 'networking-content-delivery/32/Arch_Elastic-Load-Balancing_32.svg',
    'lt': 'compute/32/Arch_Amazon-EC2_32.svg',
    
    // Default fallback
    'default': 'management-governance/32/Arch_AWS-CloudFormation_32.svg'
  };

  const normalizedType = resourceType?.toLowerCase() || 'default';
  return `/aws-service-icons/${typeMapping[normalizedType] || typeMapping.default}`;
};

/**
 * Parse Mermaid syntax to extract node type mappings
 * Returns a map of nodeId -> resourceType from :::className annotations
 */
const parseNodeTypes = (mermaidSyntax) => {
  const typeMap = {};
  if (!mermaidSyntax) return typeMap;
  
  const lines = mermaidSyntax.split('\n');
  
  lines.forEach(line => {
    // Match node definitions with class: nodeId["label"]:::className
    const match = line.trim().match(/^(\S+)\[.*?\]:::(\w+)/);
    if (match) {
      const [, nodeId, className] = match;
      typeMap[nodeId] = className; // e.g., vpc_0933701ec3e750243 -> vpc
    }
  });
  
  return typeMap;
};

/**
 * Extract original node ID from Mermaid's transformed ID
 * Mermaid adds prefixes like 'flowchart-' and suffixes like '-123'
 */
const extractOriginalId = (mermaidId) => {
  if (!mermaidId) return '';
  
  // Remove common Mermaid prefixes (flowchart-, graph-, L-, etc.)
  let id = mermaidId.replace(/^(flowchart-|graph-|L-|subgraph-)/gi, '');
  
  // Remove numeric-only suffixes added by Mermaid (e.g., -123, -456)
  // But preserve resource IDs that contain hyphens and numbers (e.g., vpc-0d67cc5f)
  // Only remove if it's a hyphen followed by ONLY digits at the end
  id = id.replace(/-(\d+)$/g, '');
  
  return id;
};

/**
 * Detect resource type from node ID or class
 * Returns the AWS service type if detected
 */
const detectResourceType = (nodeId, classNames) => {
  const idLower = nodeId.toLowerCase();
  
  // Check class names first (more reliable)
  // IMPORTANT: Order matters! Check specific patterns before generic ones
  if (classNames) {
    const classLower = classNames.toLowerCase();
    
    // Check specific resource types FIRST (before generic patterns like 'instance')
    if (classLower.includes('vpc') && !classLower.includes('subnet')) return 'vpc'; // Must check vpc before subnet
    if (classLower.includes('subnet')) return 'subnet';
    if (classLower.includes('lambda')) return 'lambda';
    if (classLower.includes('rds') || classLower.includes('database')) return 'rds';
    if (classLower.includes('dynamodb')) return 'dynamodb';
    if (classLower.includes('s3')) return 's3';
    if (classLower.includes('ami')) return 'ami';
    if (classLower.includes('natgw') || classLower.includes('nat_gateway')) return 'nat_gateway';
    if (classLower.includes('alb') || classLower.includes('lb') || classLower.includes('loadbalancer') || classLower.includes('load_balancer')) return 'load_balancer';
    if (classLower.includes('tg') || classLower.includes('targetgroup')) return 'load_balancer';
    if (classLower.includes('asg') || classLower.includes('auto_scaling')) return 'auto_scaling_group';
    if (classLower.includes('sg') || classLower.includes('security') || classLower.includes('securitygroup')) return 'security_group';
    
    // Check generic patterns LAST (these are broader and might cause false positives)
    if (classLower.includes('ec2')) return 'ec2_instance';
    if (classLower.includes('lt') || classLower.includes('launch')) return 'ec2_instance';
    // Only check 'instance' if it's clearly an EC2 instance context, not Mermaid internals
    if (classLower.match(/\bec2\b|\binstance\b/) && !classLower.includes('vpc')) return 'ec2_instance';
  }

  // Fallback: check node ID patterns (Mermaid adds prefixes like 'flowchart-', so use includes not startsWith)
  // IMPORTANT: Check VPC/Subnet patterns BEFORE EC2 patterns since they're more specific
  if (idLower.includes('vpc-') || idLower.includes('vpc_')) return 'vpc';
  if (idLower.includes('subnet-') || idLower.includes('subnet_')) return 'subnet';
  if (idLower.includes('sg-') || idLower.includes('sg_')) return 'security_group';
  if (idLower.includes('i-') || idLower.includes('i_')) return 'ec2_instance';
  if (idLower.includes('lt-') || idLower.includes('lt_')) return 'ec2_instance';
  if (idLower.includes('ami-') || idLower.includes('ami_')) return 'ami';
  if (idLower.includes('targetgroup') || idLower.includes('elasticloadbalancing')) return 'load_balancer';
  
  return null;
};

/**
 * Build a type map from node metadata (from API response)
 * Returns a map of sanitized nodeId -> resourceType
 */
const buildTypeMapFromMetadata = (nodeMetadata) => {
  const typeMap = {};
  if (!nodeMetadata || !Array.isArray(nodeMetadata)) return typeMap;
  
  nodeMetadata.forEach(node => {
    if (node.id && node.type) {
      // Sanitize the ID to match Mermaid's transformation
      const sanitizedId = node.id.replace(/[^a-zA-Z0-9_]/g, '_');
      typeMap[sanitizedId] = node.type;
    }
  });
  
  return typeMap;
};

/**
 * MermaidDiagram Component
 * Renders a Mermaid diagram with automatic initialization and error handling
 * Supports AWS service icons in node labels
 * @param {Object} props
 * @param {string} props.mermaidSyntax - The Mermaid diagram syntax
 * @param {string} props.timestamp - Timestamp of the data
 * @param {Function} props.onFullscreen - Callback for fullscreen mode
 * @param {Array} props.nodeMetadata - Array of node objects with {id, type, ...} from API response
 */
const MermaidDiagram = ({ mermaidSyntax, timestamp, onFullscreen, nodeMetadata }) => {
  const mermaidRef = useRef(null);
  const renderingRef = useRef(false);
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [zoom, setZoom] = useState(1);
  const [pan, setPan] = useState({ x: 0, y: 0 });
  const svgContainerRef = useRef(null);
  const [renderedSvg, setRenderedSvg] = useState(null);
  const [isDragging, setIsDragging] = useState(false);
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 });
  const dragContainerRef = useRef(null);

  useEffect(() => {
    if (!mermaidSyntax || !mermaidRef.current || renderingRef.current) return;

    let isMounted = true;
    renderingRef.current = true;

    const renderMermaid = async () => {
      try {
        // Generate a unique ID for this diagram
        const id = `mermaid-${Date.now()}`;
        
        if (!isMounted || !mermaidRef.current) return;
        
        // Clear previous content and remove any previous data-processed attribute
        const element = mermaidRef.current;
        element.removeAttribute('data-processed');
        element.innerHTML = '';
        
        // Create a new div for the diagram (use original syntax without HTML modifications)
        const diagramDiv = document.createElement('div');
        diagramDiv.className = 'mermaid';
        diagramDiv.id = id;
        diagramDiv.textContent = mermaidSyntax;
        element.appendChild(diagramDiv);
        
        // Simple delay to let DOM settle
        await new Promise(resolve => setTimeout(resolve, 100));
        
        if (!isMounted) return;
        
        // Render the mermaid diagram
        await mermaid.run({
          nodes: [diagramDiv],
          suppressErrors: false,
        });
        
        if (!isMounted) return;
        
        // After rendering, add AWS icons as overlay elements
        await addIconsToNodes(element);
        
        // Clone the rendered SVG to both containers
        const svgElement = element.querySelector('svg');
        if (svgElement) {
          // Store in state for use in modal
          setRenderedSvg(svgElement.outerHTML);
          
          // Also add to card view container
          if (svgContainerRef.current) {
            svgContainerRef.current.innerHTML = svgElement.outerHTML;
          }
        }
        
      } catch (error) {
        console.error('Mermaid rendering error:', error);
        
        // Display error in the visible container
        const errorHtml = `
          <div style="color: #ff4d4f; text-align: left; padding: 20px; background: #fff2f0; border: 1px solid #ffccc7; border-radius: 4px;">
            <div style="font-weight: bold; margin-bottom: 8px;"> Error rendering diagram</div>
            <div style="margin-bottom: 12px;">${error.message}</div>
            <details style="margin-top: 12px;">
              <summary style="cursor: pointer; color: #1890ff;">Show Mermaid Syntax</summary>
              <pre style="margin-top: 8px; padding: 12px; background: #f5f5f5; border-radius: 4px; overflow: auto;">${mermaidSyntax}</pre>
            </details>
          </div>
        `;
        
        if (isMounted && svgContainerRef.current) {
          svgContainerRef.current.innerHTML = errorHtml;
        }
        
        setRenderedSvg(errorHtml);
      } finally {
        if (isMounted) {
          renderingRef.current = false;
        }
      }
    };
    
    // Function to add icon overlays to rendered nodes
    const addIconsToNodes = async (container) => {
      if (!isMounted) return;
      
      try {
        // Wait a bit more for Mermaid to fully render
        await new Promise(resolve => setTimeout(resolve, 150));
        
        if (!isMounted) return;
        
        // Build type map from node metadata (PRIMARY source - from API)
        const metadataTypeMap = buildTypeMapFromMetadata(nodeMetadata);
        
        // Parse node types from original Mermaid syntax (FALLBACK source)
        const mermaidTypeMap = parseNodeTypes(mermaidSyntax);
        
        // Find all nodes in the rendered SVG
        const nodes = container.querySelectorAll('.node');
        
        nodes.forEach(node => {
          // Get node ID from the element
          const nodeId = node.id || '';
          const classAttr = node.getAttribute('class') || '';
          
          // Extract original node ID from Mermaid's transformed ID
          const originalId = extractOriginalId(nodeId);
          
          // Try to get type from metadata first (TIER 1 - most reliable)
          let resourceType = metadataTypeMap[originalId];
          
          // Fallback to Mermaid syntax parsing (TIER 2)
          if (!resourceType) {
            resourceType = mermaidTypeMap[originalId];
          }
          
          // Final fallback to pattern detection (TIER 3)
          if (!resourceType) {
            resourceType = detectResourceType(nodeId, classAttr);
          }
          
          if (resourceType) {
            const iconPath = getAWSServiceIcon(resourceType);
            
            // Find the rect element (the node box)
            const rect = node.querySelector('rect, polygon, circle, ellipse');
            if (!rect) return;
            
            // Get the position and size
            const bbox = rect.getBBox();
            
            // Create an image element
            const img = document.createElementNS('http://www.w3.org/2000/svg', 'image');
            img.setAttributeNS('http://www.w3.org/1999/xlink', 'href', iconPath);
            img.setAttribute('width', '32');
            img.setAttribute('height', '32');
            // Center the icon horizontally at the top of the node
            img.setAttribute('x', bbox.x + (bbox.width / 2) - 16); // Center icon (32/2 = 16)
            img.setAttribute('y', bbox.y + 12); // Top padding
            img.style.pointerEvents = 'none'; // Don't interfere with node interactions
            
            // Insert the icon before the text label so it appears behind
            const labelContainer = node.querySelector('.label');
            const textElements = node.querySelectorAll('.label text, text');
            
            if (labelContainer && labelContainer.parentNode) {
              labelContainer.parentNode.insertBefore(img, labelContainer);
            } else if (textElements.length > 0 && textElements[0].parentNode) {
              textElements[0].parentNode.insertBefore(img, textElements[0]);
            } else {
              node.appendChild(img);
            }
            
            // Move all text elements down to make room for icon (separate line)
            textElements.forEach(textEl => {
              // Get current transform if any
              const currentTransform = textEl.getAttribute('transform') || '';
              const currentY = parseFloat(textEl.getAttribute('y') || 0);
              
              // Move text down by icon height + spacing (more aggressive)
              if (currentTransform) {
                // If there's a transform, we need to add a translate
                const newTransform = `${currentTransform} translate(0, 35)`;
                textEl.setAttribute('transform', newTransform);
              } else {
                // Otherwise just adjust Y
                textEl.setAttribute('y', currentY + 35);
              }
            });
            
            // Also adjust label container if it exists
            if (labelContainer) {
              const currentTransform = labelContainer.getAttribute('transform') || '';
              if (currentTransform) {
                const newTransform = `${currentTransform} translate(0, 35)`;
                labelContainer.setAttribute('transform', newTransform);
              } else {
                const currentY = parseFloat(labelContainer.getAttribute('y') || 0);
                labelContainer.setAttribute('y', currentY + 35);
              }
            }
          }
        });
      } catch (error) {
        console.warn('Failed to add icons to nodes:', error);
        // Non-critical error, diagram will still display without icons
      }
    };
    
    renderMermaid();
    
    // Cleanup function
    return () => {
      isMounted = false;
      renderingRef.current = false;
    };
  }, [mermaidSyntax, nodeMetadata]); // Re-render if mermaid syntax or node metadata changes

  const handleFullscreen = () => {
    if (onFullscreen) {
      onFullscreen();
    } else {
      setIsFullscreen(true);
    }
  };

  const handleCloseFullscreen = () => {
    setIsFullscreen(false);
    setZoom(1);
    setPan({ x: 0, y: 0 }); // Reset pan when closing fullscreen
  };

  const handleZoomIn = () => {
    setZoom(prevZoom => Math.min(prevZoom + 0.25, 5)); // Max 5x zoom
  };

  const handleZoomOut = () => {
    setZoom(prevZoom => Math.max(prevZoom - 0.25, 0.25)); // Min 0.25x zoom
  };

  const handleResetZoom = () => {
    setZoom(1);
    setPan({ x: 0, y: 0 });
  };

  // Mouse wheel zoom
  const handleWheel = useCallback((e) => {
    e.preventDefault();
    const delta = e.deltaY > 0 ? -0.1 : 0.1;
    setZoom(prevZoom => Math.min(Math.max(prevZoom + delta, 0.25), 5));
  }, []);

  // Pan handlers
  const handleMouseDown = useCallback((e) => {
    if (e.button === 0) { // Left mouse button
      setIsDragging(true);
      setDragStart({ x: e.clientX - pan.x, y: e.clientY - pan.y });
      e.preventDefault();
    }
  }, [pan]);

  const handleMouseMove = useCallback((e) => {
    if (isDragging) {
      setPan({
        x: e.clientX - dragStart.x,
        y: e.clientY - dragStart.y,
      });
    }
  }, [isDragging, dragStart]);

  const handleMouseUp = useCallback(() => {
    setIsDragging(false);
  }, []);

  // Add/remove event listeners for dragging
  useEffect(() => {
    if (isDragging) {
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
      return () => {
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);
      };
    }
  }, [isDragging, handleMouseMove, handleMouseUp]);

  const renderDiagramContent = (isInModal = false) => (
    <div style={{ position: 'relative' }}>
      {/* Zoom Controls */}
      <div style={{
        position: 'absolute',
        top: '10px',
        right: '10px',
        zIndex: 10,
        display: 'flex',
        gap: '8px',
        background: 'rgba(255, 255, 255, 0.95)',
        padding: '8px',
        borderRadius: '6px',
        boxShadow: '0 2px 8px rgba(0,0,0,0.15)',
      }}>
        <Tooltip title="Zoom In (or scroll up)">
          <Button 
            icon={<ZoomInOutlined />} 
            size="small"
            onClick={handleZoomIn}
          />
        </Tooltip>
        <Tooltip title="Reset View (Zoom & Pan)">
          <Button 
            icon={<AimOutlined />}
            size="small"
            onClick={handleResetZoom}
          >
            {Math.round(zoom * 100)}%
          </Button>
        </Tooltip>
        <Tooltip title="Zoom Out (or scroll down)">
          <Button 
            icon={<ZoomOutOutlined />} 
            size="small"
            onClick={handleZoomOut}
          />
        </Tooltip>
        <Tooltip title="Drag to Pan">
          <Tag icon={<DragOutlined />} color="blue" style={{ margin: 0 }}>
            Pan Mode
          </Tag>
        </Tooltip>
      </div>
      
      {/* Diagram Container with Pan & Zoom */}
      <div 
        ref={dragContainerRef}
        onWheel={handleWheel}
        onMouseDown={handleMouseDown}
        style={{ 
          textAlign: 'center',
          backgroundColor: '#fafafa',
          padding: '20px',
          borderRadius: '6px',
          overflow: 'hidden', // Changed from 'auto' to 'hidden' for pan to work
          maxHeight: isInModal ? 'calc(100vh - 200px)' : '600px',
          minHeight: isInModal ? '500px' : '200px',
          cursor: isDragging ? 'grabbing' : 'grab',
          position: 'relative',
        }}
      >
        <div 
          ref={isInModal ? null : svgContainerRef}
          style={{ 
            display: 'inline-block',
            minWidth: '100%',
            transform: `translate(${pan.x}px, ${pan.y}px) scale(${zoom})`,
            transformOrigin: 'center center',
            transition: isDragging ? 'none' : 'transform 0.1s ease-out',
            willChange: 'transform',
          }}
          dangerouslySetInnerHTML={isInModal && renderedSvg ? { __html: renderedSvg } : undefined}
        >
          {/* Card view: SVG injected via ref, Modal view: SVG injected via dangerouslySetInnerHTML */}
        </div>
      </div>
      
      {/* Instructions */}
      <div style={{ 
        marginTop: '8px', 
        textAlign: 'center',
        color: '#999',
        fontSize: '12px'
      }}>
         Drag to pan  Scroll to zoom  Click controls for precise zoom
      </div>
    </div>
  );

  return (
    <>
      {/* Hidden container for Mermaid rendering */}
      <div ref={mermaidRef} style={{ position: 'absolute', left: '-9999px', top: 0 }} />
      
      <Card
        title={
          <Space>
            <NodeIndexOutlined />
            <Text strong>Dependency Visualization</Text>
            <Tag color="blue">Mermaid Diagram</Tag>
          </Space>
        }
        extra={
          <Space>
            {timestamp && (
              <Text type="secondary" style={{ fontSize: '12px' }}>
                Generated: {new Date(timestamp).toLocaleString()}
              </Text>
            )}
            <Tooltip title="View fullscreen">
              <Button 
                icon={<FullscreenOutlined />} 
                size="small"
                onClick={handleFullscreen}
              />
            </Tooltip>
          </Space>
        }
      >
        {renderDiagramContent(false)}
      </Card>

      {/* Fullscreen Modal */}
      <Modal
        title={
          <Space>
            <NodeIndexOutlined />
            <Text strong>Dependency Visualization (Fullscreen)</Text>
            <Tag color="blue">Mermaid Diagram</Tag>
          </Space>
        }
        open={isFullscreen}
        onCancel={handleCloseFullscreen}
        width="96vw"
        style={{ 
          top: 20,
          maxWidth: '96vw',
          paddingBottom: 0,
        }}
        styles={{ 
          body: { 
            height: 'calc(100vh - 120px)', // Full height minus header, margins, and footer space
            overflow: 'auto',
            padding: '16px',
          } 
        }}
        footer={null}
        closeIcon={<CompressOutlined />}
        destroyOnClose={false}
        centered={false}
      >
        {renderDiagramContent(true)}
      </Modal>
    </>
  );
};

MermaidDiagram.propTypes = {
  mermaidSyntax: PropTypes.string.isRequired,
  timestamp: PropTypes.string,
  onFullscreen: PropTypes.func,
  nodeMetadata: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string,
    type: PropTypes.string,
  })),
};

export default MermaidDiagram;
