import PropTypes from 'prop-types';
import { Card, Row, Col, Typography, Empty } from 'antd';
import ResourceCard from './ResourceCard';

const { Text } = Typography;

// AWS service icon mapping
const getAWSServiceIcon = (resourceType) => {
  const typeMapping = {
    // Compute
    'ec2_instance': 'compute/32/Arch_Amazon-EC2_32.svg',
    'instance': 'compute/32/Arch_Amazon-EC2_32.svg',
    'lambda': 'compute/32/Arch_AWS-Lambda_32.svg',
    'auto_scaling_group': 'compute/32/Arch_Amazon-EC2-Auto-Scaling_32.svg',
    'asg': 'compute/32/Arch_Amazon-EC2-Auto-Scaling_32.svg',
    
    // Networking
    'vpc': 'networking-content-delivery/32/Arch_Amazon-Virtual-Private-Cloud_32.svg',
    'subnet': 'networking-content-delivery/32/Arch_Amazon-Virtual-Private-Cloud_32.svg',
    'security_group': 'security-identity-compliance/32/Arch_AWS-WAF_32.svg',
    'securitygroup': 'security-identity-compliance/32/Arch_AWS-WAF_32.svg',
    'load_balancer': 'networking-content-delivery/32/Arch_Elastic-Load-Balancing_32.svg',
    'loadbalancer': 'networking-content-delivery/32/Arch_Elastic-Load-Balancing_32.svg',
    'alb': 'networking-content-delivery/32/Arch_Elastic-Load-Balancing_32.svg',
    'route53': 'networking-content-delivery/32/Arch_Amazon-Route-53_32.svg',
    'cloudfront': 'networking-content-delivery/32/Arch_Amazon-CloudFront_32.svg',
    'internet_gateway': 'networking-content-delivery/32/Arch_Amazon-Virtual-Private-Cloud_32.svg',
    'nat_gateway': 'networking-content-delivery/32/Arch_Amazon-Virtual-Private-Cloud_32.svg',
    'route_table': 'networking-content-delivery/32/Arch_Amazon-Virtual-Private-Cloud_32.svg',
    
    // Database
    'rds': 'database/32/Arch_Amazon-RDS_32.svg',
    'database': 'database/32/Arch_Amazon-RDS_32.svg',
    'dynamodb': 'database/32/Arch_Amazon-DynamoDB_32.svg',
    'redshift': 'database/32/Arch_Amazon-Redshift_32.svg',
    
    // Storage
    's3': 'storage/32/Arch_Amazon-Simple-Storage-Service_32.svg',
    'ebs': 'storage/32/Arch_Amazon-Elastic-Block-Store_32.svg',
    'efs': 'storage/32/Arch_Amazon-Elastic-File-System_32.svg',
    
    // Default fallback
    'default': 'management-governance/32/Arch_AWS-CloudFormation_32.svg'
  };

  const normalizedType = resourceType?.toLowerCase() || 'default';
  return `/aws-service-icons/${typeMapping[normalizedType] || typeMapping.default}`;
};

const ResourcesByCategory = ({ infrastructureData, searchTerm }) => {
  if (!infrastructureData) {
    return (
      <div style={{ 
        backgroundColor: 'white',
        borderRadius: '16px',
        border: '1px solid #e8e8e8',
        padding: '48px'
      }}>
        <Empty
          description={
            <Text type="secondary" style={{ fontSize: '16px' }}>
              No infrastructure data available
            </Text>
          }
          style={{ padding: '48px 0' }}
        />
      </div>
    );
  }

  // Handle different response structures
  let resources = {};
  
  const hasDiscoveredResources = infrastructureData.resources && infrastructureData.resources.length > 0;
  const managedResourceCount = infrastructureData.managed_state ? 
    Object.keys(infrastructureData.managed_state.resources || {}).length : 0;

  if (infrastructureData.managed_state && managedResourceCount > 0) {
    resources = infrastructureData.managed_state.resources || {};
  } else if (hasDiscoveredResources) {
    resources = {};
    infrastructureData.resources.forEach((resource, index) => {
      resources[resource.id || `resource-${index}`] = {
        id: resource.id,
        name: resource.name || resource.id,
        type: resource.type,
        status: resource.status,
        properties: {
          aws_details: resource.properties
        },
        createdAt: resource.createdAt,
        dependencies: resource.properties?.dependencies
      };
    });
  }

  // Filter out step_reference resources and apply search
  const filteredResources = Object.values(resources)
    .filter(r => r.type !== 'step_reference')
    .filter(r => !searchTerm || r.id.toLowerCase().includes(searchTerm.toLowerCase()));

  // Group resources by type (category)
  const resourcesByCategory = filteredResources.reduce((acc, resource) => {
    const type = resource.type || 'unknown';
    if (!acc[type]) {
      acc[type] = [];
    }
    acc[type].push(resource);
    return acc;
  }, {});

  if (Object.keys(resourcesByCategory).length === 0) {
    return (
      <div style={{ 
        backgroundColor: 'white',
        borderRadius: '16px',
        border: '1px solid #e8e8e8',
        padding: '48px'
      }}>
        <Empty
          description={
            <Text type="secondary" style={{ fontSize: '16px' }}>
              {searchTerm ? 'No resources match your search' : 'No resources found'}
            </Text>
          }
          style={{ padding: '48px 0' }}
        />
      </div>
    );
  }

  return (
    <div style={{ display: 'flex', flexDirection: 'column', gap: '24px' }}>
      {Object.entries(resourcesByCategory).map(([category, resources]) => (
        <Card
          key={category}
          className="category-card"
          bodyStyle={{ padding: '24px' }}
          title={
            <div className="category-header">
              <Text strong style={{ 
                textTransform: 'capitalize', 
                fontSize: '18px',
                color: '#1a1a1a'
              }}>
                {category.replace('_', ' ')} ({resources.length})
              </Text>
            </div>
          }
        >
          <Row gutter={[16, 16]}>
            {resources.map((resource) => (
              <Col key={resource.id} xs={24} sm={12} lg={8} xl={6}>
                <ResourceCard 
                  resource={resource} 
                  searchTerm={searchTerm}
                  iconUrl={getAWSServiceIcon(resource.type)}
                />
              </Col>
            ))}
          </Row>
        </Card>
      ))}
    </div>
  );
};

ResourcesByCategory.propTypes = {
  infrastructureData: PropTypes.shape({
    resources: PropTypes.array,
    managed_state: PropTypes.object,
  }),
  searchTerm: PropTypes.string,
};

ResourcesByCategory.defaultProps = {
  searchTerm: '',
};

export default ResourcesByCategory;