import React from 'react';
import { Card, Space, Button, Tag, Progress, Typography } from 'antd';
import { 
  RobotOutlined, 
  CheckOutlined, 
  CloseOutlined 
} from '@ant-design/icons';
import PropTypes from 'prop-types';
import ExecutionStep from './ExecutionStep';
import InlineRecovery from './InlineRecovery';

const { Text } = Typography;

/**
 * Complete execution plan component with status header, steps, and actions
 * Handles the entire execution plan display with consistent styling
 */
const ExecutionPlan = ({
  steps = [],
  logs = [],
  recoveryOptions = {},
  isExecuting = false,
  executionProgress = 0,
  executionElapsed = '00:00',
  executionCompleted = false,
  onConfirm = null,
  onCancel = null,
  onProceedWithRecovery = null,
  onAbortRecovery = null,
  showConfirmButton = true,
  showCancelButton = true,
  confirmButtonText = 'Confirm & Execute',
  children = null,
  title = 'Execution Plan',
  icon = <RobotOutlined />,
  className = '',
  ...props
}) => {
  // Function to get logs for a specific step
  const getStepLogs = (stepId, stepIndex) => {
    if (!logs || logs.length === 0) return [];
    
    // Filter logs by stepId, or if stepId is not available, try to match by step index
    return logs.filter(log => {
      if (log.stepId) {
        // Try exact match first
        if (log.stepId === stepId) return true;
        // Try step-{index} format
        if (log.stepId === `step-${stepIndex}`) return true;
        // Try just the index number
        if (log.stepId === stepIndex.toString()) return true;
        // Try step_{index} format
        if (log.stepId === `step_${stepIndex}`) return true;
      }
      // If no stepId in the log, for now return all logs for the first step (index 0)
      // This is a fallback for logs that don't have step association
      return stepIndex === 0 && !log.stepId;
    });
  };

  const renderStatusHeader = () => {
    if (!isExecuting && !executionCompleted) return null;

    const statusStyle = executionCompleted ? {
      background: '#d4edda', 
      border: '1px solid #28a745',
      boxShadow: '0 0 8px rgba(40, 167, 69, 0.3)',
    } : {
      background: '#f8f9fa', 
      border: '1px solid #dee2e6'
    };

    return (
      <div style={{ 
        padding: '16px', 
        borderRadius: '8px',
        ...statusStyle
      }}>
        <div style={{ 
          display: 'flex', 
          justifyContent: 'space-between', 
          alignItems: 'center',
          marginBottom: '12px' 
        }}>
          <Text strong className={executionCompleted ? 'status-success' : 'status-running'}>
            {executionCompleted ? 'Execution Completed Successfully' : 'Execution in Progress'}
          </Text>
          <Text type="secondary" style={{ fontFamily: 'monospace' }}>
            {executionElapsed}
          </Text>
        </div>
        <Progress 
          percent={executionProgress} 
          status={executionCompleted ? 'success' : 'active'} 
          strokeColor={executionCompleted ? '#28a745' : '#007bff'}
          trailColor="#e9ecef"
        />
      </div>
    );
  };

  const renderActionButtons = () => {
    if (isExecuting || executionCompleted) {
      return isExecuting ? <Tag color="processing">Executing...</Tag> : null;
    }

    return (
      <Space>
        {showConfirmButton && onConfirm && (
          <Button 
            type="primary" 
            icon={<CheckOutlined />}
            onClick={onConfirm}
            size="large"
          >
            {confirmButtonText}
          </Button>
        )}
        
        {showCancelButton && onCancel && (
          <Button 
            icon={<CloseOutlined />}
            onClick={onCancel}
            size="large"
          >
            Cancel
          </Button>
        )}
      </Space>
    );
  };  // Determine card styling based on execution state

  const cardStyle = {
    marginTop: 16,
    ...(executionCompleted && {
      borderColor: '#28a745',
      boxShadow: '0 4px 12px rgba(40, 167, 69, 0.15)',
    })
  };

  return (
    <div className={`execution-plan-container ${className} ${executionCompleted ? 'execution-success' : ''}`}>
      <Card 
        title={<>{icon} {title}</>} 
        style={cardStyle}
        {...props}
      >
        <Space direction="vertical" size="large" style={{ width: '100%' }}>
          {/* Execution Status Header */}
          {renderStatusHeader()}
          
          {/* Execution Steps */}
          <div style={{ display: 'flex', flexDirection: 'column', gap: '12px' }}>
            {steps.map((step, index) => (
              <React.Fragment key={`${step.id || index}-${step.status || 'pending'}`}>
                <ExecutionStep
                  step={step}
                  index={index}
                  logs={getStepLogs(step.id, index)}
                />
                {/* Recovery Options - inline after each step */}
                {recoveryOptions[step.id] && (
                  <InlineRecovery 
                    stepId={step.id}
                    recoveryData={recoveryOptions[step.id]}
                    onProceedWithRecovery={onProceedWithRecovery}
                    onAbortRecovery={onAbortRecovery}
                  />
                )}
              </React.Fragment>
            ))}
          </div>
          
          {/* Action Buttons */}
          {renderActionButtons()}
          
          {children && React.Children.map(children, child => {
            // Skip ExecutionLogs component
            if (React.isValidElement(child) && child.type?.name === 'ExecutionLogs') {
              return null;
            }
            return child;
          })}
        </Space>
      </Card>
    </div>
  );
};

ExecutionPlan.propTypes = {
  steps: PropTypes.arrayOf(PropTypes.object),
  logs: PropTypes.arrayOf(PropTypes.shape({
    type: PropTypes.string,
    message: PropTypes.string,
    timestamp: PropTypes.instanceOf(Date),
    stepId: PropTypes.string,
  })),
  recoveryOptions: PropTypes.object,
  isExecuting: PropTypes.bool,
  executionProgress: PropTypes.number,
  executionElapsed: PropTypes.string,
  executionCompleted: PropTypes.bool,
  onConfirm: PropTypes.func,
  onCancel: PropTypes.func,
  onProceedWithRecovery: PropTypes.func,
  onAbortRecovery: PropTypes.func,
  showConfirmButton: PropTypes.bool,
  showCancelButton: PropTypes.bool,
  confirmButtonText: PropTypes.string,
  children: PropTypes.node,
  title: PropTypes.string,
  icon: PropTypes.node,
  className: PropTypes.string,
};

export default ExecutionPlan;