// Mock data service for development/testing
class MockDataService {
  constructor() {
    this.mockEnabled = process.env.REACT_APP_ENABLE_FEATURE_MOCK === 'true';
    console.log('MockDataService initialized. Mock enabled:', this.mockEnabled);
  }

  // Simulate API delay
  async delay(ms = 500) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  // Load mock data from public/mocks folder
  async loadMockData(filename) {
    try {
      const response = await fetch(`/mocks/${filename}`);
      if (!response.ok) {
        throw new Error(`Failed to load mock data: ${filename}`);
      }
      return await response.json();
    } catch (error) {
      console.error('Error loading mock data:', error);
      throw error;
    }
  }

  // Mock: Get dependency graph
  async getDependencyGraph(source = null) {
    await this.delay(800); // Simulate network delay

    try {
      let mockFile;
      
      if (source === 'live') {
        // Load live discovery mock data
        mockFile = 'mock-discovery-live.json';
      } else {
        // Load default graph mock data (generate-graph button)
        mockFile = 'mock-generate-graph.json';
      }

      const data = await this.loadMockData(mockFile);
      
      // Transform the mock data to match expected API response format
      return this.transformGraphData(data);
    } catch (error) {
      console.error('Mock getDependencyGraph error:', error);
      throw new Error(`Failed to load mock dependency graph: ${error.message}`);
    }
  }

  // Transform mock graph data to match API response format
  transformGraphData(mockData) {
    // Real API response format includes both mermaid syntax and graph structure
    // We should return the data as-is if it already has the expected format
    if (mockData.mermaid && mockData.graph) {
      return {
        mermaid: mockData.mermaid,
        graph: mockData.graph, // Include graph with nodes metadata
        timestamp: mockData.timestamp || new Date().toISOString(),
        metadata: mockData.metadata || this.generateDefaultMetadata(mockData),
        source: mockData.source || 'unknown',
        format: mockData.format || 'mermaid',
      };
    }

    // Legacy: If mock data only has mermaid syntax (old format)
    if (mockData.mermaid) {
      return {
        mermaid: mockData.mermaid,
        graph: null, // No graph metadata available
        timestamp: mockData.timestamp || new Date().toISOString(),
        metadata: mockData.metadata || this.generateDefaultMetadata(mockData),
      };
    }

    // Legacy: If mock data has graph structure but no mermaid (shouldn't happen in real API)
    if (mockData.graph && mockData.graph.edges) {
      const mermaidSyntax = this.convertGraphToMermaid(mockData.graph);
      return {
        mermaid: mermaidSyntax,
        graph: mockData.graph,
        timestamp: new Date().toISOString(),
        metadata: this.generateMetadataFromGraph(mockData.graph),
      };
    }

    throw new Error('Invalid mock data format');
  }

  // Convert graph edges/nodes to Mermaid syntax
  convertGraphToMermaid(graph) {
    const lines = ['graph TD'];
    const nodes = new Set();
    const nodeTypes = {};

    // Collect all nodes and their types
    graph.edges.forEach(edge => {
      nodes.add(edge.source);
      nodes.add(edge.target);
      
      // Detect node types and store with SANITIZED IDs (for Mermaid output)
      const sanitizedSource = edge.source.replace(/[^a-zA-Z0-9_]/g, '_');
      const sanitizedTarget = edge.target.replace(/[^a-zA-Z0-9_]/g, '_');
      
      // Detect type for source
      if (edge.source.startsWith('vpc-')) nodeTypes[sanitizedSource] = 'vpc';
      else if (edge.source.startsWith('subnet-')) nodeTypes[sanitizedSource] = 'subnet';
      else if (edge.source.startsWith('sg-')) nodeTypes[sanitizedSource] = 'sg';
      else if (edge.source.startsWith('i-')) nodeTypes[sanitizedSource] = 'ec2';
      else if (edge.source.startsWith('lt-')) nodeTypes[sanitizedSource] = 'lt';
      else if (edge.source.startsWith('nat-')) nodeTypes[sanitizedSource] = 'natgw';
      else if (edge.source.includes('targetgroup')) nodeTypes[sanitizedSource] = 'tg';
      else if (edge.source.includes('loadbalancer') || edge.source.includes('app/') || edge.source.includes('net/')) nodeTypes[sanitizedSource] = 'lb';
      else if (edge.source.includes('database') || edge.source.includes('cluster')) nodeTypes[sanitizedSource] = 'rds';
      
      // Detect type for target
      if (edge.target.startsWith('vpc-')) nodeTypes[sanitizedTarget] = 'vpc';
      else if (edge.target.startsWith('subnet-')) nodeTypes[sanitizedTarget] = 'subnet';
      else if (edge.target.startsWith('sg-')) nodeTypes[sanitizedTarget] = 'sg';
      else if (edge.target.startsWith('i-')) nodeTypes[sanitizedTarget] = 'ec2';
      else if (edge.target.startsWith('lt-')) nodeTypes[sanitizedTarget] = 'lt';
      else if (edge.target.startsWith('nat-')) nodeTypes[sanitizedTarget] = 'natgw';
      else if (edge.target.includes('targetgroup')) nodeTypes[sanitizedTarget] = 'tg';
      else if (edge.target.includes('loadbalancer') || edge.target.includes('app/') || edge.target.includes('net/')) nodeTypes[sanitizedTarget] = 'lb';
      else if (edge.target.includes('database') || edge.target.includes('cluster')) nodeTypes[sanitizedTarget] = 'rds';
    });

    // Add blank line
    lines.push('');

    // Define nodes with their types
    nodes.forEach(node => {
      const type = nodeTypes[node] || 'default';
      const sanitizedId = node.replace(/[^a-zA-Z0-9_]/g, '_');
      lines.push(`    ${sanitizedId}["${node}"]:::${type}`);
    });

    // Add blank line
    lines.push('');

    // Add edges
    graph.edges.forEach(edge => {
      const sourceId = edge.source.replace(/[^a-zA-Z0-9_]/g, '_');
      const targetId = edge.target.replace(/[^a-zA-Z0-9_]/g, '_');
      lines.push(`    ${sourceId} --> ${targetId}`);
    });

    // Add blank line
    lines.push('');

    // Add class definitions with colors
    lines.push('    classDef vpc fill:#e1f5fe');
    lines.push('    classDef subnet fill:#b3e5fc');
    lines.push('    classDef ec2 fill:#fff3e0');
    lines.push('    classDef sg fill:#f3e5f5');
    lines.push('    classDef lb fill:#e8f5e8');
    lines.push('    classDef asg fill:#fce4ec');
    lines.push('    classDef natgw fill:#e0f2f1');
    lines.push('    classDef tg fill:#f1f8e9');
    lines.push('    classDef rds fill:#fff9c4');
    lines.push('    classDef lt fill:#fce4ec');
    lines.push('    classDef default fill:#f5f5f5');

    return lines.join('\n');
  }

  // Generate metadata from graph structure
  generateMetadataFromGraph(graph) {
    const nodeTypes = {};
    const nodes = new Set();

    graph.edges.forEach(edge => {
      nodes.add(edge.source);
      nodes.add(edge.target);
      
      // Count node types
      const sourceType = this.detectNodeType(edge.source);
      const targetType = this.detectNodeType(edge.target);
      
      nodeTypes[sourceType] = (nodeTypes[sourceType] || 0) + 1;
      nodeTypes[targetType] = (nodeTypes[targetType] || 0) + 1;
    });

    return {
      nodeCount: nodes.size,
      edgeCount: graph.edges.length,
      resourceTypeCounts: nodeTypes,
      bottlenecks: this.generateMockBottlenecks(nodes),
    };
  }

  // Detect node type from ID
  detectNodeType(nodeId) {
    if (nodeId.startsWith('vpc-')) return 'VPC';
    if (nodeId.startsWith('subnet-')) return 'Subnet';
    if (nodeId.startsWith('sg-')) return 'Security Group';
    if (nodeId.startsWith('i-')) return 'EC2 Instance';
    if (nodeId.startsWith('lt-')) return 'Launch Template';
    if (nodeId.includes('targetgroup')) return 'Target Group';
    if (nodeId.includes('loadbalancer')) return 'Load Balancer';
    return 'Other';
  }

  // Generate mock bottlenecks
  generateMockBottlenecks(nodes) {
    const bottlenecks = [];
    let count = 0;
    
    // Add a few mock bottlenecks
    nodes.forEach(node => {
      if (count >= 3) return; // Limit to 3 bottlenecks
      
      if (node.startsWith('vpc-')) {
        bottlenecks.push({
          resource: node,
          dependents: Math.floor(Math.random() * 20) + 10,
          impact: 'high',
          reason: 'Multiple resources depend on this VPC',
        });
        count++;
      }
    });

    return bottlenecks;
  }

  // Generate default metadata
  generateDefaultMetadata(mockData) {
    return {
      nodeCount: 0,
      edgeCount: 0,
      resourceTypeCounts: {},
      bottlenecks: [],
    };
  }
}

const mockDataService = new MockDataService();
export default mockDataService;
