// API service for centralized HTTP requests
import mockDataService from './mockDataService';

class ApiService {
  constructor() {
    // Load API base URL from environment variable, fallback to relative path
    this.baseURL = process.env.REACT_APP_API_BASE_URL || '/api';
    
    // Remove trailing slash if present
    this.baseURL = this.baseURL.replace(/\/$/, '');
    
    // Check if mock mode is enabled
    this.mockEnabled = process.env.REACT_APP_ENABLE_FEATURE_MOCK === 'true';
    
    console.log('ApiService initialized with baseURL:', this.baseURL);
    console.log('Mock mode enabled:', this.mockEnabled);
  }

  // Generic API call method with error handling
  async call(endpoint, options = {}) {
    const url = `${this.baseURL}${endpoint}`;
    
    const config = {
      headers: {
        'Content-Type': 'application/json',
        ...options.headers,
      },
      ...options,
    };

    try {
      const response = await fetch(url, config);
      
      if (!response.ok) {
        // Try to get error details from response body
        let errorMessage = `HTTP ${response.status}: ${response.statusText}`;
        let errorDetails = null;
        
        try {
          const errorText = await response.text();
          if (errorText) {
            try {
              const errorData = JSON.parse(errorText);
              if (errorData.error) {
                errorMessage = errorData.error;
              } else if (errorData.message) {
                errorMessage = errorData.message;
              } else if (errorData.detail) {
                errorMessage = errorData.detail;
              }
              errorDetails = errorData;
            } catch (jsonError) {
              // If response isn't JSON, use the text as error message
              if (errorText.length < 200) { // Only use short error messages
                errorMessage = errorText;
              }
            }
          }
        } catch (readError) {
          // If we can't read the response body, stick with the status message
          console.warn('Could not read error response body:', readError);
        }
        
        const error = new Error(errorMessage);
        error.status = response.status;
        error.statusText = response.statusText;
        error.details = errorDetails;
        throw error;
      }

      const responseText = await response.text();
      
      if (!responseText) {
        throw new Error('Empty response from server');
      }

      try {
        return JSON.parse(responseText);
      } catch (jsonError) {
        console.error('JSON parse error:', jsonError);
        console.error('Response text:', responseText);
        throw new Error(`Invalid JSON response: ${jsonError.message}`);
      }
    } catch (error) {
      console.error(`API call failed for ${endpoint}:`, error);
      
      // Enhance error messages for common network issues
      if (error.name === 'TypeError' && error.message === 'Failed to fetch') {
        const enhancedError = new Error('Unable to connect to server. Please check your internet connection and try again.');
        enhancedError.originalError = error;
        enhancedError.isNetworkError = true;
        throw enhancedError;
      }
      
      if (error.name === 'AbortError') {
        const enhancedError = new Error('Request timed out. Please try again.');
        enhancedError.originalError = error;
        enhancedError.isTimeoutError = true;
        throw enhancedError;
      }
      
      throw error;
    }
  }

  // Agent operations
  async processAgentRequest(request, dryRun = true) {
    return this.call('/agent/process', {
      method: 'POST',
      body: JSON.stringify({ 
        request, 
        dry_run: dryRun 
      }),
    });
  }

  async executeConfirmedPlan(decisionId) {
    return this.call('/agent/execute', {
      method: 'POST',
      body: JSON.stringify({ decisionId: decisionId }),
    });
  }

  // State management operations
  async getInfrastructureState(discoveredOnly = false) {
    const params = discoveredOnly ? '?discovered_only=true' : '';
    return this.call(`/state${params}`);
  }

  async discoverInfrastructure() {
    return this.call('/discover', {
      method: 'POST',
    });
  }

  async exportState(includeDiscovered = false) {
    const params = includeDiscovered ? '?include_discovered=true' : '';
    const response = await fetch(`${this.baseURL}/export${params}`);
    
    if (!response.ok) {
      throw new Error(`Export failed: ${response.statusText}`);
    }
    
    return response.blob();
  }

  // Graph operations
  async getDependencyGraph(source = null) {
    // Use mock data if enabled
    if (this.mockEnabled) {
      console.log('Using mock data for getDependencyGraph, source:', source);
      return mockDataService.getDependencyGraph(source);
    }
    
    // API now only returns mermaid format
    // source can be 'live' for live discovery or null for default
    const params = source ? `?source=${source}` : '';
    return this.call(`/graph${params}`);
  }

  // Conflict detection
  async detectConflicts(autoResolve = false) {
    const params = new URLSearchParams({
      auto_resolve: autoResolve.toString(),
    });
    
    return this.call(`/conflicts?${params}`);
  }

  // Deployment planning
  async generateDeploymentPlan(targetResources = [], includeLevels = true) {
    const params = new URLSearchParams();
    
    if (targetResources.length > 0) {
      targetResources.forEach(resource => {
        params.append('target_resources', resource);
      });
    }
    
    params.set('include_levels', includeLevels.toString());
    
    return this.call(`/plan?${params}`);
  }

  // Utility methods for file operations
  downloadFile(blob, filename) {
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = filename;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    window.URL.revokeObjectURL(url);
  }

  getCurrentTimestamp() {
    return new Date().toISOString().split('T')[0];
  }

  // Configuration methods
  getBaseURL() {
    return this.baseURL;
  }

  getConfig() {
    return {
      baseURL: this.baseURL,
      wsURL: process.env.REACT_APP_WS_URL,
      environment: process.env.NODE_ENV || 'development',
    };
  }
}

// Create a singleton instance
const apiService = new ApiService();

export default apiService;