import { useState, useEffect } from 'react';
import { Button } from 'antd';
import { 
  CheckOutlined, 
  CloseOutlined, 
  ClockCircleOutlined,
  LoadingOutlined,
  DownOutlined,
  UpOutlined,
  CodeOutlined
} from '@ant-design/icons';
import PropTypes from 'prop-types';

/**
 * Individual execution step component with consistent styling
 * Shows step number, content, and status with proper visual hierarchy
 */
const ExecutionStep = ({ 
  step, 
  index, 
  logs = [],
  className = '',
  ...props 
}) => {
  const [logsExpanded, setLogsExpanded] = useState(false);
  const [parametersExpanded, setParametersExpanded] = useState(false);
  
  const stepStatus = step.status || 'pending';

  // Auto-expand logs on failure or when there are new logs for running steps
  useEffect(() => {
    if (stepStatus === 'failed' || 
        stepStatus.includes('recovery') ||
        (stepStatus === 'running' && logs.length > 0)) {
      setLogsExpanded(true);
    }
  }, [stepStatus, logs.length]);

  const getStatusIcon = (status) => {
    switch (status) {
      case 'completed': 
        return <CheckOutlined style={{ color: 'white', fontSize: '12px' }} />;
      case 'failed': 
        return <CloseOutlined style={{ color: 'white', fontSize: '12px' }} />;
      case 'running': 
        return <LoadingOutlined spin style={{ color: 'white', fontSize: '12px' }} />;
      case 'recovery-generating':
        return <LoadingOutlined spin style={{ color: 'white', fontSize: '12px' }} />;
      case 'recovery-pending':
        return <ClockCircleOutlined style={{ color: '#212529', fontSize: '12px' }} />;
      case 'recovery-in-progress':
        return <LoadingOutlined spin style={{ color: 'white', fontSize: '12px' }} />;
      case 'pending':
      default: 
        return <ClockCircleOutlined style={{ color: '#6c757d', fontSize: '12px' }} />;
    }
  };

  const getStepNumberClass = (status) => {
    return `execution-step-number ${status}`;
  };

  const stepTitle =  step.name || step.description || step.action || `Step ${index + 1}`;
  const stepDescription = step.details || step.description || 'Executing infrastructure operation...';

  return (
    <div 
      className={`execution-step ${className}`} 
      data-step-status={stepStatus}
      {...props}
    >
      <div className="execution-step-header">
        <div className={getStepNumberClass(stepStatus)}>
          <div className="step-number-content">
            <span className="step-number-text">{index + 1}</span>
          </div>
        </div>
        
        <div className="execution-step-content">
          <div className="execution-step-title">
            {stepTitle}
          </div>
          
          <div className="execution-step-description">
            {stepDescription}
          </div>
          
          <div className="execution-step-meta">
            <span className="step-duration">
              Duration: {step.duration || 'Estimating...'}
            </span>
            <span className="step-dependencies">
              Status: {stepStatus}
            </span>
            {step.dependencies && (
              <span className="step-dependencies">
                Dependencies: {step.dependencies}
              </span>
            )}
          </div>
          
          {/* Tool Parameters Section */}
          {(step.id || step.mcpTool || step.toolParameters || step.parameters) && (
            <div style={{ marginTop: '12px' }}>
              <Button
                type="text"
                size="small"
                icon={<CodeOutlined />}
                onClick={() => setParametersExpanded(!parametersExpanded)}
                style={{ 
                  padding: '4px 8px',
                  height: 'auto',
                  fontSize: '12px',
                  color: '#1890ff'
                }}
              >
                {parametersExpanded ? 'Hide' : 'Show'} Step Details
              </Button>
              
              {parametersExpanded && (
                <div style={{
                  marginTop: '8px',
                  padding: '12px',
                  background: '#f6f8fa',
                  borderRadius: '6px',
                  border: '1px solid #e1e4e8',
                  fontSize: '13px',
                  overflow: 'auto',
                  maxHeight: '300px'
                }}>
                  {/* Step ID Section */}
                  {step.id && (
                    <div style={{ 
                      marginBottom: (step.mcpTool || step.toolParameters || step.parameters) ? '12px' : '0',
                      paddingBottom: (step.mcpTool || step.toolParameters || step.parameters) ? '12px' : '0',
                      borderBottom: (step.mcpTool || step.toolParameters || step.parameters) ? '1px solid #e1e4e8' : 'none'
                    }}>
                      <div style={{ fontWeight: '600', color: '#24292e', marginBottom: '4px' }}>
                        Step ID
                      </div>
                      <div style={{ 
                        fontFamily: 'Monaco, Menlo, "Ubuntu Mono", monospace',
                        color: '#0366d6',
                        fontSize: '12px'
                      }}>
                        {step.id}
                      </div>
                    </div>
                  )}
                  
                  {/* MCP Tool Section */}
                  {step.mcpTool && (
                    <div style={{ 
                      marginBottom: (step.toolParameters || step.parameters) ? '12px' : '0',
                      paddingBottom: (step.toolParameters || step.parameters) ? '12px' : '0',
                      borderBottom: (step.toolParameters || step.parameters) ? '1px solid #e1e4e8' : 'none'
                    }}>
                      <div style={{ fontWeight: '600', color: '#24292e', marginBottom: '4px' }}>
                        MCP Tool
                      </div>
                      <div style={{ 
                        fontFamily: 'Monaco, Menlo, "Ubuntu Mono", monospace',
                        color: '#0366d6',
                        fontSize: '12px'
                      }}>
                        {step.mcpTool}
                      </div>
                    </div>
                  )}
                  
                  {/* Parameters Section */}
                  {(step.toolParameters || step.parameters) && (
                    <div>
                      <div style={{ fontWeight: '600', color: '#24292e', marginBottom: '8px' }}>
                        Parameters
                      </div>
                      <pre style={{ 
                        margin: 0,
                        whiteSpace: 'pre-wrap',
                        wordBreak: 'break-word',
                        fontFamily: 'Monaco, Menlo, "Ubuntu Mono", monospace',
                        fontSize: '12px',
                        color: '#24292e'
                      }}>
                        {JSON.stringify(step.toolParameters || step.parameters, null, 2)}
                      </pre>
                    </div>
                  )}
                </div>
              )}
            </div>
          )}
        </div>
        
        <div className="step-status-icon-container">
          {getStatusIcon(stepStatus)}
        </div>
        
        {/* Expand logs button - only show if there are logs or step is running/completed/failed */}
        {(logs.length > 0 || ['running', 'completed', 'failed'].includes(stepStatus)) && (
          <Button
            type="text"
            size="small"
            icon={logsExpanded ? <UpOutlined /> : <DownOutlined />}
            onClick={() => setLogsExpanded(!logsExpanded)}
            className="expand-logs-button"
          />
        )}
      </div>
      
      {/* Step logs section */}
      {logsExpanded && (
        <div className="execution-step-logs">
          <div className="logs-content">
            {logs.length > 0 ? (
              logs.map((log, logIndex) => (
                <div key={logIndex} className={`step-log-entry level-${log.type}`}>
                  <span className="timestamp">
                    {log.timestamp?.toLocaleTimeString() || new Date().toLocaleTimeString()}
                  </span>
                  <span className="level">[{log.type?.toUpperCase() || 'INFO'}]</span>
                  <span className="message">{log.message}</span>
                </div>
              ))
            ) : (
              <div className="no-logs">No logs available for this step</div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

ExecutionStep.propTypes = {
  step: PropTypes.shape({
    id: PropTypes.string,
    action: PropTypes.string,
    name: PropTypes.string,
    description: PropTypes.string,
    details: PropTypes.string,
    mcpTool: PropTypes.string,
    toolParameters: PropTypes.object,
    parameters: PropTypes.object,
    status: PropTypes.oneOf([
      'pending', 
      'running', 
      'completed', 
      'failed',
      'recovery-generating',
      'recovery-pending',
      'recovery-in-progress'
    ]),
    duration: PropTypes.string,
    dependencies: PropTypes.string,
  }).isRequired,
  index: PropTypes.number.isRequired,
  logs: PropTypes.arrayOf(PropTypes.shape({
    type: PropTypes.string,
    message: PropTypes.string,
    timestamp: PropTypes.instanceOf(Date),
    stepId: PropTypes.string,
  })),
  className: PropTypes.string,
};

export default ExecutionStep;