import { Card, Tag, Typography, Empty } from 'antd';
import PropTypes from 'prop-types';

const { Text } = Typography;

/**
 * Execution logs component with consistent styling and formatting
 * Displays execution logs with timestamps and status indicators
 */
const ExecutionLogs = ({
  logs = [],
  title = 'Execution Logs',
  maxHeight = 300,
  showTimestamps = true,
  className = '',
  style = {},
  ...props
}) => {
  if (!logs.length) {
    return (
      <Card 
        size="small"
        title={title}
        className={className}
        style={{ 
          background: '#f8f9fa',
          border: '1px solid #dee2e6',
          ...style
        }}
        {...props}
      >
        <Empty 
          description="No logs available" 
          style={{ padding: '20px 0' }}
        />
      </Card>
    );
  }

  const getTagColor = (type) => {
    switch (type) {
      case 'success': return 'success';
      case 'error': return 'error';
      case 'warning': return 'warning';
      case 'info': return 'processing';
      default: return 'default';
    }
  };

  return (
    <Card 
      size="small"
      title={title}
      className={className}
      style={{ 
        background: '#f8f9fa',
        border: '1px solid #dee2e6',
        ...style
      }}
      {...props}
    >
      <div style={{ maxHeight, overflow: 'auto' }}>
        {logs.map((log, index) => (
          <div key={index} style={{ 
            marginBottom: 8,
            padding: '8px 12px',
            background: 'white',
            borderRadius: '4px',
            border: '1px solid #e9ecef'
          }}>
            <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
              {showTimestamps && (
                <Tag 
                  color={getTagColor(log.type)}
                  size="small"
                >
                  {log.timestamp instanceof Date 
                    ? log.timestamp.toLocaleTimeString() 
                    : log.timestamp
                  }
                </Tag>
              )}
              <Text style={{ fontSize: '0.9rem', flex: 1 }}>
                {log.message}
              </Text>
              {log.type && !showTimestamps && (
                <Tag 
                  color={getTagColor(log.type)}
                  size="small"
                >
                  {log.type.toUpperCase()}
                </Tag>
              )}
            </div>
          </div>
        ))}
      </div>
    </Card>
  );
};

ExecutionLogs.propTypes = {
  logs: PropTypes.arrayOf(PropTypes.shape({
    message: PropTypes.string.isRequired,
    timestamp: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
    type: PropTypes.oneOf(['success', 'error', 'warning', 'info', 'default']),
  })),
  title: PropTypes.string,
  maxHeight: PropTypes.number,
  showTimestamps: PropTypes.bool,
  className: PropTypes.string,
  style: PropTypes.object,
};

export default ExecutionLogs;