import { useState } from 'react';
import { Button, Card, Tag, Space, Typography } from 'antd';
import { 
  ExclamationCircleOutlined,
  PlayCircleOutlined,
  StopOutlined,
  LoadingOutlined,
  DownOutlined,
  UpOutlined
} from '@ant-design/icons';
import PropTypes from 'prop-types';

const { Text, Paragraph } = Typography;

/**
 * Inline Recovery component - displays recovery options directly within the execution plan
 * Based on the old UI's inline recovery implementation
 */
const InlineRecovery = ({ 
  stepId, 
  recoveryData, 
  onProceedWithRecovery, 
  onAbortRecovery 
}) => {
  const [selectedOptionIndex, setSelectedOptionIndex] = useState(null);

  if (!recoveryData) return null;

  // Recovery in progress state
  if (recoveryData.inProgress) {
    return (
      <Card 
        size="small" 
        className="inline-recovery-card recovery-in-progress"
        style={{ 
          borderLeft: '4px solid #17a2b8',
          backgroundColor: '#f8f9fa'
        }}
      >
        <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
          <LoadingOutlined spin style={{ color: '#17a2b8' }} />
          <Text strong>Executing Recovery...</Text>
          {recoveryData.selectedOption === 'skip' ? (
            <Text type="secondary">Skipping step</Text>
          ) : (
            <Text type="secondary">Option {parseInt(recoveryData.selectedOption) + 1}</Text>
          )}
        </div>
        {recoveryData.progressMessage && (
          <div style={{ marginTop: '8px' }}>
            <Text type="secondary">{recoveryData.progressMessage}</Text>
          </div>
        )}
      </Card>
    );
  }

  if (!recoveryData.visible) return null;

  const { failureContext, recoveryOptions } = recoveryData;

  const handleOptionSelect = (optionIndex) => {
    setSelectedOptionIndex(optionIndex);
  };

  const handleProceed = () => {
    if (selectedOptionIndex !== null && onProceedWithRecovery) {
      onProceedWithRecovery(stepId, selectedOptionIndex);
    }
  };

  const handleAbort = () => {
    if (onAbortRecovery) {
      onAbortRecovery(stepId);
    }
  };

  return (
    <Card 
      size="small" 
      className="inline-recovery-card"
      style={{ 
        borderLeft: '4px solid #ffc107',
        backgroundColor: '#fffef0',
        marginTop: '8px'
      }}
    >
      {/* Recovery Header */}
      <div style={{ display: 'flex', alignItems: 'center', gap: '8px', marginBottom: '12px' }}>
        <ExclamationCircleOutlined style={{ color: '#ffc107', fontSize: '16px' }} />
        <Text strong style={{ color: '#856404' }}>Step Failed - Recovery Options Available</Text>
      </div>

      {/* Failure Details */}
      <div style={{ marginBottom: '12px', padding: '8px', backgroundColor: '#f8d7da', borderRadius: '4px' }}>
        <Text strong>Error: </Text>
        <Text>{failureContext?.errorMessage || 'Unknown error'}</Text>
        <br />
        <Text strong>Step: </Text>
        <Text>{failureContext?.stepName || 'Unknown'}</Text>
        {failureContext?.toolName && (
          <>
            <span> | </span>
            <Text strong>Tool: </Text>
            <Text>{failureContext.toolName}</Text>
          </>
        )}
      </div>

      {/* AI Analysis (if available) */}
      {failureContext?.aiAnalysis && (
        <div style={{ marginBottom: '12px', padding: '8px', backgroundColor: '#d1ecf1', borderRadius: '4px' }}>
          <div style={{ marginBottom: '4px' }}>
            <Text strong>Root Cause: </Text>
            <Text>{failureContext.aiAnalysis.rootCause || 'Not available'}</Text>
          </div>
          <div>
            <Text strong>Recommendation: </Text>
            <Text>{failureContext.aiAnalysis.recommendation || 'Not available'}</Text>
          </div>
        </div>
      )}

      {/* Recovery Options */}
      <div style={{ marginBottom: '12px' }}>
        <Text strong style={{ marginBottom: '8px', display: 'block' }}>Choose Recovery Action:</Text>
        
        <Space direction="vertical" size="small" style={{ width: '100%' }}>
          {recoveryOptions && recoveryOptions.map((option, index) => (
            <RecoveryOption
              key={index}
              option={option}
              index={index}
              isSelected={selectedOptionIndex === index}
              onSelect={() => handleOptionSelect(index)}
            />
          ))}
          
          {/* Skip option */}
          <RecoveryOption
            option={{
              action: 'Skip This Step',
              reasoning: 'Skip this step and continue with the next step in the plan.',
              riskLevel: 'medium',
              successProbability: 0.7,
              details: 'Note: May affect steps that depend on this step\'s output.'
            }}
            index="skip"
            isSelected={selectedOptionIndex === 'skip'}
            onSelect={() => handleOptionSelect('skip')}
          />
        </Space>
      </div>

      {/* Action Buttons */}
      <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '8px' }}>
        <Button 
          type="default"
          danger
          icon={<StopOutlined />}
          onClick={handleAbort}
        >
          Abort Execution
        </Button>
        <Button 
          type="primary"
          icon={<PlayCircleOutlined />}
          disabled={selectedOptionIndex === null}
          onClick={handleProceed}
        >
          Proceed with Selected Option
        </Button>
      </div>
    </Card>
  );
};

/**
 * Individual recovery option component
 */
const RecoveryOption = ({ option, index, isSelected, onSelect }) => {
  const [showDetails, setShowDetails] = useState(false);
  
  const successProb = Math.round((option.successProbability || 0) * 100);
  const riskLevel = (option.riskLevel || 'medium').toLowerCase();
  
  const isMultiStep = option.action === 'multi_step_recovery' && option.multiStepPlan && option.multiStepPlan.length > 0;
  
  const getRiskColor = (risk) => {
    switch (risk) {
      case 'low': return 'green';
      case 'high': return 'red';
      case 'medium':
      default: return 'orange';
    }
  };

  return (
    <Card 
      size="small"
      className={`recovery-option ${isSelected ? 'selected' : ''}`}
      onClick={onSelect}
      style={{ 
        cursor: 'pointer',
        border: isSelected ? '2px solid #1890ff' : '1px solid #d9d9d9',
        backgroundColor: isSelected ? '#f0f7ff' : '#fff'
      }}
    >
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', marginBottom: '8px' }}>
        <Text strong>
          {option.action}
          {isMultiStep && (
            <Tag size="small" color="blue" style={{ marginLeft: '8px' }}>
              {option.totalSteps || option.multiStepPlan.length} steps
            </Tag>
          )}
        </Text>
        <Space size="small">
          <Tag color="blue">{successProb}% Success</Tag>
          <Tag color={getRiskColor(riskLevel)}>{option.riskLevel || 'Medium'} Risk</Tag>
        </Space>
      </div>
      
      <Paragraph style={{ marginBottom: isMultiStep || option.details ? '8px' : '0', color: '#666' }}>
        {option.reasoning || 'No reasoning provided'}
      </Paragraph>

      {/* Single-step details */}
      {!isMultiStep && (option.newTool || option.modifiedParameters) && (
        <div style={{ fontSize: '12px', color: '#888', marginBottom: '8px' }}>
          {option.newTool && `New Tool: ${option.newTool}`}
          {option.newTool && option.modifiedParameters && ' | '}
          {option.modifiedParameters && `Parameters: ${JSON.stringify(option.modifiedParameters)}`}
        </div>
      )}

      {/* Option details */}
      {option.details && !isMultiStep && (
        <div style={{ fontSize: '12px', color: '#888' }}>
          {option.details}
        </div>
      )}

      {/* Multi-step details */}
      {isMultiStep && (
        <div style={{ marginTop: '8px' }}>
          <Button 
            type="text" 
            size="small"
            icon={showDetails ? <UpOutlined /> : <DownOutlined />}
            onClick={(e) => {
              e.stopPropagation();
              setShowDetails(!showDetails);
            }}
            style={{ padding: '0', height: 'auto', fontSize: '12px' }}
          >
            {showDetails ? 'Hide' : 'Show'} Multi-Step Plan ({option.totalSteps || option.multiStepPlan.length} steps)
          </Button>
          
          {showDetails && (
            <div style={{ marginTop: '8px', paddingLeft: '16px', borderLeft: '2px solid #f0f0f0' }}>
              {option.multiStepPlan.map((step, stepIndex) => (
                <div key={stepIndex} style={{ marginBottom: '8px', fontSize: '12px' }}>
                  <div style={{ display: 'flex', alignItems: 'flex-start', gap: '8px' }}>
                    <div style={{ 
                      minWidth: '16px', 
                      height: '16px', 
                      borderRadius: '50%', 
                      backgroundColor: '#1890ff', 
                      color: 'white', 
                      display: 'flex', 
                      alignItems: 'center', 
                      justifyContent: 'center',
                      fontSize: '10px',
                      marginTop: '2px'
                    }}>
                      {step.stepOrder || stepIndex + 1}
                    </div>
                    <div style={{ flex: 1 }}>
                      <div style={{ fontWeight: 'bold' }}>{step.toolName}</div>
                      <div style={{ color: '#666' }}>{step.purpose}</div>
                      {step.parameters && Object.keys(step.parameters).length > 0 && (
                        <div style={{ color: '#888', marginTop: '2px' }}>
                          Parameters: {JSON.stringify(step.parameters, null, 2)}
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      )}
    </Card>
  );
};

InlineRecovery.propTypes = {
  stepId: PropTypes.string.isRequired,
  recoveryData: PropTypes.shape({
    visible: PropTypes.bool,
    inProgress: PropTypes.bool,
    selectedOption: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    progressMessage: PropTypes.string,
    failureContext: PropTypes.shape({
      errorMessage: PropTypes.string,
      stepName: PropTypes.string,
      toolName: PropTypes.string,
      aiAnalysis: PropTypes.shape({
        rootCause: PropTypes.string,
        recommendation: PropTypes.string,
      }),
    }),
    recoveryOptions: PropTypes.arrayOf(PropTypes.shape({
      action: PropTypes.string,
      reasoning: PropTypes.string,
      riskLevel: PropTypes.string,
      successProbability: PropTypes.number,
      newTool: PropTypes.string,
      modifiedParameters: PropTypes.object,
      details: PropTypes.string,
      multiStepPlan: PropTypes.array,
      totalSteps: PropTypes.number,
    })),
  }),
  onProceedWithRecovery: PropTypes.func,
  onAbortRecovery: PropTypes.func,
};

RecoveryOption.propTypes = {
  option: PropTypes.object.isRequired,
  index: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  isSelected: PropTypes.bool,
  onSelect: PropTypes.func,
};

export default InlineRecovery;