import { Card, Space, Tag, Typography } from 'antd';
import { WarningOutlined } from '@ant-design/icons';
import PropTypes from 'prop-types';

const { Text } = Typography;

/**
 * BottlenecksList Component
 * Displays performance bottlenecks with color-coded impact levels
 */
const BottlenecksList = ({ bottlenecks }) => {
  if (!bottlenecks || bottlenecks.length === 0) {
    return null;
  }

  const getImpactColor = (impact) => {
    switch (impact) {
      case 'critical': return '#ff4d4f';
      case 'high': return '#faad14';
      case 'medium': return '#1890ff';
      case 'low': return '#52c41a';
      default: return '#d9d9d9';
    }
  };

  const getImpactIcon = (impact) => {
    return <WarningOutlined style={{ color: getImpactColor(impact) }} />;
  };

  return (
    <Card 
      title={
        <Space>
          <WarningOutlined style={{ color: '#faad14' }} />
          <Text strong>Performance Bottlenecks Detected</Text>
        </Space>
      }
      style={{ marginTop: 16 }}
    >
      <Space direction="vertical" size="middle" style={{ width: '100%' }}>
        {bottlenecks.map((bottleneck, index) => (
          <Card 
            key={index}
            size="small"
            style={{ 
              borderLeft: `4px solid ${getImpactColor(bottleneck.impact)}`,
              backgroundColor: '#fafafa'
            }}
          >
            <Space direction="vertical" size="small" style={{ width: '100%' }}>
              <Space>
                {getImpactIcon(bottleneck.impact)}
                <Text strong style={{ fontSize: '14px' }}>
                  {bottleneck.resourceId}
                </Text>
                <Tag color={getImpactColor(bottleneck.impact)}>
                  {bottleneck.impact.toUpperCase()}
                </Tag>
              </Space>
              <div style={{ paddingLeft: '24px' }}>
                <Text type="secondary" style={{ fontSize: '12px' }}>
                  Type: {bottleneck.resourceType}  {bottleneck.dependentCount} dependent{bottleneck.dependentCount !== 1 ? 's' : ''}
                </Text>
              </div>
            </Space>
          </Card>
        ))}
      </Space>
    </Card>
  );
};

BottlenecksList.propTypes = {
  bottlenecks: PropTypes.arrayOf(
    PropTypes.shape({
      resourceId: PropTypes.string.isRequired,
      resourceType: PropTypes.string.isRequired,
      dependentCount: PropTypes.number.isRequired,
      impact: PropTypes.oneOf(['low', 'medium', 'high', 'critical']).isRequired,
      dependents: PropTypes.arrayOf(PropTypes.string),
    })
  ),
};

export default BottlenecksList;
