import { Alert } from 'antd';
import PropTypes from 'prop-types';

/**
 * Reusable status alert component with consistent styling
 * Automatically determines alert type based on message content
 */
const StatusAlert = ({ 
  message, 
  type = 'auto', 
  showIcon = true, 
  closable = false,
  style = {},
  ...props 
}) => {
  // Auto-determine alert type based on message content
  const getAlertType = () => {
    if (type !== 'auto') return type;
    
    const msg = message.toLowerCase();
    if (msg.includes('error') || msg.includes('failed') || msg.includes('fail')) {
      return 'error';
    } else if (msg.includes('warning') || msg.includes('warn')) {
      return 'warning';
    } else if (msg.includes('success') || msg.includes('completed') || msg.includes('processed')) {
      return 'success';
    } else if (msg === 'ready') {
      return 'info';
    } else {
      return 'success';
    }
  };

  const alertType = getAlertType();

  // Clean up message display - remove "Error:" prefix for better UX
  const displayMessage = typeof message === 'string' && message.startsWith('Error: ') 
    ? message.substring(7) // Remove "Error: " prefix
    : message;

  return (
    <Alert 
      message={displayMessage} 
      type={alertType}
      showIcon={showIcon}
      closable={closable}
      style={{ 
        borderRadius: 6,
        fontSize: 14,
        marginBottom: 16,
        ...style 
      }}
      {...props}
    />
  );
};

StatusAlert.propTypes = {
  message: PropTypes.oneOfType([PropTypes.string, PropTypes.node]).isRequired,
  type: PropTypes.oneOf(['auto', 'success', 'info', 'warning', 'error']),
  showIcon: PropTypes.bool,
  closable: PropTypes.bool,
  style: PropTypes.object,
};

export default StatusAlert;