import PropTypes from 'prop-types';
import { Typography, Button, Input } from 'antd';
import { ReloadOutlined, SearchOutlined } from '@ant-design/icons';

const { Text, Title } = Typography;

const StateHeader = ({ 
  onRefresh, 
  refreshing, 
  searchTerm, 
  onSearchChange, 
  totalResources, 
  totalCategories 
}) => {
  return (
    <div style={{ 
      marginBottom: '32px',
      backgroundColor: 'white',
      padding: '24px',
      borderRadius: '16px',
      boxShadow: '0 2px 8px rgba(0, 0, 0, 0.04)',
      border: '1px solid #e8e8e8'
    }}>
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '16px' }}>
        <Title level={2} style={{ margin: 0, color: '#1a1a1a', fontWeight: 700 }}>
          Infrastructure Resources
        </Title>
        <Button
          type="primary"
          icon={<ReloadOutlined spin={refreshing} />}
          onClick={onRefresh}
          loading={refreshing}
          style={{
            borderRadius: '8px',
            height: '40px',
            backgroundColor: '#1890ff',
            borderColor: '#1890ff',
          }}
        >
          Refresh
        </Button>
      </div>
      
      <div style={{ display: 'flex', gap: '16px', alignItems: 'center' }}>
        <Input
          placeholder="Search resources by ID..."
          prefix={<SearchOutlined />}
          value={searchTerm}
          onChange={onSearchChange}
          style={{
            maxWidth: '300px',
            borderRadius: '8px',
          }}
          allowClear
        />
        <Text type="secondary">
          {totalResources} resource{totalResources !== 1 ? 's' : ''} in {totalCategories} categories
        </Text>
      </div>
    </div>
  );
};

StateHeader.propTypes = {
  onRefresh: PropTypes.func.isRequired,
  refreshing: PropTypes.bool,
  searchTerm: PropTypes.string,
  onSearchChange: PropTypes.func,
  totalResources: PropTypes.number,
  totalCategories: PropTypes.number,
};

StateHeader.defaultProps = {
  refreshing: false,
  searchTerm: '',
  onSearchChange: () => {},
  totalResources: 0,
  totalCategories: 0,
};

export default StateHeader;